import { Injectable } from '@angular/core';
import { User } from '../../models/user.model';
import { HttpClient } from '@angular/common/http';
import { URL_SERVICES } from '../../config/config';
import { Router } from '@angular/router';
import { UploadFileService } from '../uploadFile/upload-file.service';

import swal from 'sweetalert';

import { Observable, Subject, asapScheduler, pipe, of, from, interval, merge, fromEvent } from 'rxjs';
import { map, filter, scan } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class UserService {

	user: User;
	token: string;
  desde: number = 0;

  	constructor(public http: HttpClient,
  				public router: Router,
          public _uploadFileService: UploadFileService
  	) {
  		this.loadStorage();
  	}

  	isLogged() {
  		return (this.token.length > 5) ? true : false;
  	}

  	loadStorage() {
  		if(localStorage.getItem('token')) {
  			this.token = localStorage.getItem('token');
  			this.user = JSON.parse(localStorage.getItem('user'));
  		} else {
  			this.token = '';
  			this.user = null;
  		}
  	}

    guardarStorage(id:string, user:User) {
      localStorage.setItem('id', id);
      localStorage.setItem('user', JSON.stringify(user));

      this.user = user;
    }

  	logout() {
  		this.user = null;
  		this.token = '';

  		localStorage.removeItem('token');
  		localStorage.removeItem('user');

  		this.router.navigate(['/login']);
  	}

  	login(user: User, remember:boolean = false) {

  		if(remember) {
  			localStorage.setItem('email', user.email);
  		} else {
  			localStorage.removeItem('email');
  		}

  		let url = URL_SERVICES + '/login';
  		return this.http.post(url, user)
  			.pipe(map((resp: any) => {

  				localStorage.setItem('id', resp.id);
  				localStorage.setItem('token', resp.token);
  				localStorage.setItem('user', JSON.stringify(resp.user));

  				return true;

  			}))

  	}

  	createUser(user: User) {

  		let url = URL_SERVICES + '/user';

  		return this.http.post(url, user).pipe(map((resp: any) => {
  				swal('Usuario creado', user.email, 'success');
  				return resp.user;
  			}))

  	}

    updateUser(user: User) {

      let url = URL_SERVICES + '/user/' + user._id;

      return this.http.put(url, user)
        .pipe(map((resp: any) => {
          // this.user = resp.user;

          if(user._id === this.user._id) {
            
            this.guardarStorage(resp.user._id, resp.user);

          }

          swal('Usuario actualizado',user.name,'success');

          return true;
        }));
    }

    cambiarImg(file: File, id: string) {

      this._uploadFileService.uploadFile(file, 'profile', id)
        .then((resp: any) => {
          console.log(resp.user.img);
          this.user.imgProfile = resp.user.img;
          swal('Imagen actualizada', this.user.name, 'success');
          this.guardarStorage(id, this.user);
        }).catch(e => console.log(e));

    }

    loadUsers(desde:number = 0) {

      let url = URL_SERVICES + '/user?desde=' + desde;

      return this.http.get(url)

    }

    buscarUsuarios(termino:string) {

      let url = URL_SERVICES + '/search/collection/user/' + termino;

      return this.http.get(url).pipe(
          map((resp: any) => resp.user)
        )

    }

    borrarUser(id:string) {

      let url = URL_SERVICES + '/user/' + id;

      return this.http.delete(url).pipe(
          map(resp => {
            swal('Usuario borrado','El usuario ha sido eliminado correctamente', 'success');
            return true;
          })
        )

    }

}
