import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
import { UserService } from '../services/service.index';
import { User } from '../models/user.model';

declare function init_plugins();
declare const gapi:any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

	email: string;
	remember: boolean = false;

	auth2: any;

  	constructor(public router: Router, public _userService: UserService) {}

 	ngOnInit() {
 		init_plugins();
 		this.googleInit();

 		this.email = localStorage.getItem('email') || '';

 		if(this.email.length > 0) {
 			this.remember = true;
 		}
 	}

 	attachSingIn(elem) {

 		this.auth2.attachClickHandler(elem, {}, (googleUser) => {
 			let profile = googleUser.getBasicProfile();
 			console.log(profile);
 		});

 		this.attachSingIn(document.getElementById('btnGoogle'));

 	}

 	googleInit() {
 		gapi.load('auth2', () => {
 			this.auth2 = gapi.auth2.init({
 				client_id: '339672755777-l47dvjafpdtd71f4g0f5r739tkh5s16h.apps.googleusercontent.com',
 				cookiepolicy: 'single_host_origin',
 				scope: 'profile email'
 			});
 		})
 	}

 	ingresar(form: NgForm) {

 		if(form.invalid) {
 			return;
 		}

 		let user = new User(null, form.value.email, form.value.password);

 		this._userService.login(user, form.value.remember)
 			.subscribe(resp => this.router.navigate(['/dashboard']))

 		//this.router.navigate(['/dashboard']);
 	}


}
