import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { User } from '../../models/user.model';
import { Deck } from '../../models/deck.model';
import { UserService } from '../../services/service.index';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {

	users: User;
	desde: number = 0;
	loading: boolean = true;

	totalRegistros: number = 0;

  	constructor(public _userService: UserService) { }

  	ngOnInit() {
  		this.cargarUsuarios();
  	}

  	cargarUsuarios() {

  		this.loading = true;

  		this._userService.loadUsers(this.desde)
  			.subscribe((resp: any) => {

  				this.totalRegistros = resp.total;
  				this.users = resp.users;
  				this.loading = false;

  			})
  	}

  	buscarUsuario(termino:string) {

  		if(termino.length <= 0) {
  			this.cargarUsuarios();
  			return;
  		}

  		this.loading = true;
  		
  		this._userService.buscarUsuarios(termino)
  			.subscribe((user: User) => {
  				this.users = user;
  				this.loading = false;
  			});

  	}

  	cambiarDesde(valor:number) {
  		let desde = this.desde + valor;
  		console.log(desde);

  		if(desde >= this.totalRegistros) {
  			return;
  		}

  		if(desde < 0) {
  			return;
  		}

  		desde += valor;
  		this.cargarUsuarios();

  	}

  	borrarUsuario(user: User) {

  		if (user._id === this._userService.user._id) {
  			swal('No puede borrar usuario', 'No se puede borrar a sí mismo', 'error');
  			return;
  		}

  		this._userService.borrarUser(user._id)
  					.subscribe(resp => {
  						console.log(resp);
  						this.cargarUsuarios();
  					});

  		// swal({
  		// 	title: '¿Está seguro?',
  		// 	text: 'Está a punto de borrar a ' + user.name,
  		// 	icon: 'warning',
  		// 	buttons: true,
  		// 	dangerMode: true
  		// })
  		// .then(borrar => {
  		// 	if(borrar) {
  		// 		this._userService.borrarUser(user._id)
  		// 			.subscribe(resp => {
  		// 				console.log(resp);
  		// 				this.cargarUsuarios();
  		// 			});
  		// 	}
  		// })

  	}

  	guardarUsuario(user:User) {

  		this._userService.updateUser(user)
  			.subscribe();

  	}

}
