import { Injectable } from '@angular/core';

import { Deck } from '../../models/deck.model';
import { HttpClient } from '@angular/common/http';
import { URL_SERVICES } from '../../config/config';
import { Router } from '@angular/router';
import { UploadFileService } from '../uploadFile/upload-file.service';

import swal from 'sweetalert';

import { Observable, Subject, asapScheduler, pipe, of, from, interval, merge, fromEvent } from 'rxjs';
import { map, filter, scan } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DeckService {

	  deck:Deck;
	  desde:number = 0;
    totalDecks:number = 0;

  	constructor(public http: HttpClient,
  				    public router: Router,
          		public _uploadFileService: UploadFileService) { }

  	

    cargarDecks() {

      let url = URL_SERVICES + '/deck';

      return this.http.get(url).pipe(
          map((resp:any) => {
            this.totalDecks = resp.total;
            return resp.deck;
          })
        )
    }

    obternerDeck(id:string) {
      
      let url = URL_SERVICES + '/deck/' + id;

      return this.http.get(url).pipe(
        map((resp:any) => resp.deck)
      )

    }

    borrarDeck(id:string) {

      let url = URL_SERVICES + '/deck/' + id;

      return this.http.delete(url).pipe(
         map((resp:any) => swal('Deck eliminado','Se ha borrado su deck correctamente', 'success'))
      );
    }

    crearDeck(title:string) {

      let url = URL_SERVICES + '/deck';

      return this.http.post(url, {title}).pipe(
        map((resp:any) => {
          resp.deck
        })
      )
    }

    buscarDeck(termino:string) {

      let url = URL_SERVICES + '/search/collection/deck/' + termino;

      return this.http.get(url).pipe(
          map((resp: any) => resp.deck)
        )
    }

    actualizarDeck(deck:Deck) {

      let url = URL_SERVICES + '/deck/' + deck._id;

      return this.http.put(url, deck).pipe(
        (resp:any) => resp.deck
      )

    }


}
