import { Component, OnInit } from '@angular/core';
import { Deck } from '../../models/deck.model';
import { DeckService } from '../../services/service.index';

declare var swal:any;

@Component({
  selector: 'app-deck',
  templateUrl: './deck.component.html',
  styleUrls: ['./deck.component.css']
})
export class DeckComponent implements OnInit {

	deck:Deck[] = [];

  constructor(public _deckService:DeckService) { }

  ngOnInit() {
  	this.cargarDecks();
  }

  buscarDeck(termino:string) {

  	if(termino.length <= 0) {
  		this.cargarDecks();
  		return;
  	}

  	this._deckService.buscarDeck(termino)
  		.subscribe((deck:any) => this.deck = deck);
  }

  cargarDecks() {
  	this._deckService.cargarDecks()
  		.subscribe((deck:any) => this.deck = deck);
  }

  borrarDeck(deck:Deck) {
  	this._deckService.borrarDeck(deck[1]._id)
  		.subscribe( () => {
  			this.cargarDecks()
  		})
  }

  guardarDeck(deck:Deck) {
  	this._deckService.actualizarDeck(deck)
  		.subscribe();
  }

  crearDeck() {

  	swal({
  		title: 'Crear deck',
  		text: 'Ingrese el término',
  		content: 'input',
  		icon: 'info',
  		buttons: true,
  		dangerMode: true
  	}).then((valor:string) => {
  		if(!valor || valor.length === 0) {
  			return;
  		}

  		this._deckService.crearDeck(valor)
  			.subscribe(() => this.cargarDecks())
  	})

  }

  

  

}
